<template>
  <div id="vapp">

 <Row>
        <i-col span="24">
          <!-- 顶部菜单 -->

   <Menu mode="horizontal" theme="dark" active-key="1">
   <Row type="flex">
        <i-col offset="3"  span="4" style="color:white;font-size:18px"><Icon type="social-vimeo"></Icon>
        hydra帮助手册</i-col>
       
   
        <i-col offset="10" span="6"> <Menu-item key="1">
            <Icon type="ios-paper"></Icon>
            起步
        </Menu-item><Menu-item key="2">
            <Icon type="ios-people"></Icon>
            构建
        </Menu-item><Menu-item key="4">
            <Icon type="settings"></Icon>
            高级
        </Menu-item></i-col>
    </Row>
       
        
        
    </Menu>
          </i-col>       
    </Row>
     <Row>      
        <i-col offset="2" span="4">
<!-- 导航 -->
 <Menu theme="light" active-name="1">
        <MenuGroup v-for="(val) in menus.menus"  v-bind:key="val" :title="val.title" active-key="1">
            <MenuItem v-for="(item,index) in val.items" :active-key="index"  v-bind:key="item.title" :name="index" @click.native="show(item.url)" >               
                {{item.title}}
            </MenuItem>            
        </MenuGroup>
    </Menu>
        </i-col>
        <i-col span="10">
<!-- 内容 -->
 <iframe id="show-iframe" :src="menus.url" frameborder="0"></iframe>

        </i-col>
         <i-col span="2"> </i-col>
    </Row>
  </div>
</template>

<script>
export default {
  name: 'App',
    data () {
            return {    
              menus:{"url":"http://www.baidu.com","menus":[
                {"title":"起步","items":[{"title":"概述","url":"http://163.com"},{"title":"hello world"}, {"title":"六大服务器"}, {"title":"启动原理"}]},
                {"title":"构建","items":[{"title":"API服务"},{"title":"WEB服务"},{"title":"WS服务"},{"title":"RPC服务"},{"title":"CRON服务"},{"title":"MQC服务"}]}
                ]}    
            }
        },
    mounted(){
        this.$fetch("/menus.json")
        .then(res => {
            this.menus=res
        })
        .catch(err => {
          console.debug(err)
        });
      this.resetIframe();
  },
  methods:{
    resetIframe:function(){
      const oIframe = document.getElementById('show-iframe');
      const deviceWidth = document.documentElement.clientWidth;
      const deviceHeight = document.documentElement.clientHeight;
      oIframe.style.width = deviceWidth + 'px';
      oIframe.style.height = deviceHeight + 'px';
  
		if (oIframe.attachEvent){
        oIframe.attachEvent("onload", function(){
        var body = document.getElementById('show-iframe').contentWindow.document.getElementsByClassName("markdown-preview")  
        body[0].style.left="35%"
			});
		} else {
        oIframe.onload = function(){
        var body = document.getElementById('show-iframe').contentWindow.document.getElementsByClassName("markdown-preview")       
        body[0].style.left="35%"
			};
		}
    },
    show:function(url){
      this.menus.url=url
      this.resetIframe()
    }
  }
}
</script>

